.header {
display: flex;
justify-content: space-between;
align-items: center;
&__logo {
width: 250px;
img {
    width: 100%;
}
 }
&__official {
    color: #FFF;
    font-size: 12px;
    font-weight: 500;
    text-align: center;
    span {
        display: block;
        font-size: 18px;
    }
}
&__phone {
display: block;
color: #FFF;
font-size: 18px;
font-weight: 700;
text-decoration: none;
&:hover {
color: blue;
}
}
&__contacts {
display: flex;
flex-direction: column;
align-items: flex-end;
}
}